.menu {
  display: flex;
}

.menu-item {
  color: #005ba1;
  font-weight: 400;
  padding: 10px;
  margin-top: 10px;
  font-size: 18px;
}

@media only screen and (max-width: 600px) {
  .menu-item {
    display: none;
  }
  .search-title {
    font-size: 22px;
    text-align: center;
    color: white;
    font-weight: 400;
    text-shadow: 1px 1px white;
  }

  .search-input {
    font-size: 10px;
  }
  .search-input {
    text-align: center;
  }
  .card-box {
    max-width: 300px;
  }
  .search-desc {
    color: white;
    text-align: center;
    font-size: 17px;
    text-shadow: 1px 1px white;
  }
  .button-search {
    padding: 8px !important;
    font-size: 10px !important;
  }
}

.search-title {
  text-shadow: 1px 1px black;
  text-transform: uppercase;
  color: black;
}

.search-input {
  background-color: white;
  border-radius: 10px;
  font-size: 20;
  border: none;
}

.search-desc {
  text-shadow: 1px 1px black;
  font-size: 20px;
}

.card-box {
  max-width: 380px;
}

.button-search {
  background: linear-gradient(#4ea2e3, #1684d9);
  color: white !important;
  font-weight: 600 !important;
  padding: 10px !important;
  margin: 10px !important;
}

.button-more {
  margin-left: auto !important;
  display: block !important;
  margin-right: auto !important;
  margin-top: 50px !important;
  background: linear-gradient(#4ea2e3, #1684d9) !important;
  padding: 10px 30px !important;
  color: white !important;
  font-weight: 600 !important;
}
